import { chunk } from 'lodash'
import React, { FC } from 'react'
import { generateNotifies, generateNotifiesForRound } from '../live_videos/Notifies'
import { useVideoCustomState } from '../live_videos/util'
import { useLastSelectedVideoComments, useLastSelectedVideoInfo } from '../util/fb'
import './Rounds.css'

export const RoundsWidget: FC = () => {
  const [videoInfo] = useLastSelectedVideoInfo()
  const [comments] = useLastSelectedVideoComments()
  const [videoState] = useVideoCustomState(videoInfo!.id)()
  const notifies = generateNotifies(comments, videoInfo!.page.name)
  const notifiesForRound = generateNotifiesForRound(notifies, videoState)
  const separateRounds = chunk(notifiesForRound, videoState.roundSize)
  return (
    <div id="rounds-log">
      {separateRounds.map((round, i) => (
        <div>
          <h2>{i === 0 ? 'Pa\'horita' : 'Pal\'rato'}</h2>
          <ul>
          {round.map(n => (
            <li>{n.from.name}</li>
          ))}
          </ul>
        </div>
      ))}
    </div>
  )
}
