import { FacebookOutlined, SaveOutlined, SettingOutlined } from '@ant-design/icons'
import { Breadcrumb, Button, Form, Input, Layout, Modal } from 'antd'
import Search from 'antd/lib/input/Search'
import React, { FC, useState } from 'react'
import { useParams } from 'react-router-dom'
import { useFacebookPage } from '../hooks/useFacebookPage'
import { PageContext } from '../PageContext'

interface Values {
  chatboxUrl: string
}

const GiveawayData = () => {
  const [postUrl, setPostUrl] = useState<string>()
  return (
    <>
      <Search prefix={<FacebookOutlined />} placeholder="Post Url" defaultValue={postUrl} enterButton={<SaveOutlined />} size="large" onSearch={(value) => setPostUrl(value)} />
    </>
  )
}

const { Content } = Layout

export const AdvancedGiveaway: FC = () => {
  const { pageId } = useParams<{ pageId: string }>()
  const [, pageData] = useFacebookPage(pageId)

  return (
    <PageContext.Provider value={pageData}>
      <Content style={{ margin: '0 16px' }}>
        <Breadcrumb style={{ margin: '16px 0' }}>
          <Breadcrumb.Item>{pageData?.name}</Breadcrumb.Item>
          <Breadcrumb.Item>Advanced Giveaway</Breadcrumb.Item>
        </Breadcrumb>
        <div className="site-layout-background" style={{ padding: 24, minHeight: 360 }}>
          <GiveawayData />
        </div>
      </Content>
    </PageContext.Provider>
  )
}
