import React, { FC, useEffect, useState } from 'react'
import './App.css'
import { Layout, Menu, Spin } from 'antd'
import { DashboardOutlined, FacebookOutlined, GifOutlined, UnorderedListOutlined } from '@ant-design/icons'
import { BrowserRouter, Link, Route, Switch, useLocation } from 'react-router-dom'
import { LiveVideosDashboard } from './LiveVideoDashboard'
import { PageSelector } from './PageSelector'
import { PageToken, PageTokensResponse } from './definitions'
import { fetchStreamlabsConfig, isValidUrl, useChatBoxUrlState } from './util/streamlabs'
import { getPagesTokens } from './util/fb'
import { Widgets } from './widgets/Widgets'
import { AdvancedGiveaway } from './giveaway/AdvancedGiveaway'

const { Footer, Sider } = Layout
const { SubMenu } = Menu

function getSelectedMenuItemKey(path: string) {
  if (path.includes('/live_videos')) {
    const [, pageId] = path.split('/')
    return [`page-${pageId}-live_videos`]
  }
  if (path.includes('/advanced_giveaway')) {
    const [, pageId] = path.split('/')
    return [`page-${pageId}-advanced_giveaway`]
  }
  return ['home']
}

interface SidebarProps {
  showPageSelector: boolean
}

const Sidebar: FC<SidebarProps> = ({ showPageSelector }) => {
  const [loading, setLoading] = useState(true)
  const [pages, setPages] = useState<PageToken[]>([])
  useEffect(() => {
    const effect = async () => {
      if (showPageSelector) {
        const tokensResponse: PageTokensResponse = await getPagesTokens()
        setPages(tokensResponse.data)
        setLoading(false)
      }
    }
    effect()
  }, [loading, showPageSelector])
  const [collapsed, setCollapsed] = useState(false)
  const onCollapse = () => setCollapsed(!collapsed)
  const match = useLocation()

  console.log(match)

  return (
    <Sider collapsible collapsed={collapsed} onCollapse={onCollapse}>
      <div className="logo" />
      <Menu theme="dark" defaultSelectedKeys={['home']} mode="inline" selectedKeys={getSelectedMenuItemKey(match.pathname)}>
        <Menu.Item key="home" icon={<DashboardOutlined />}>
          <Link to="/">Dashboard</Link>
        </Menu.Item>
        {pages.map((p) => (
          <SubMenu key={`page-${p.id}`} icon={<FacebookOutlined />} title={p.name}>
            <Menu.Item key={`page-${p.id}-live_videos`} icon={<UnorderedListOutlined />}>
              <Link to={`/${p.id}/live_videos`}>Live Videos</Link>
            </Menu.Item>
            <Menu.Item key={`page-${p.id}-advanced_giveaway`} icon={<GifOutlined />}>
              <Link to={`/${p.id}/advanced_giveaway`}>Advanced Giveaway</Link>
            </Menu.Item>
          </SubMenu>
        ))}
      </Menu>
    </Sider>
  )
}

const NormalPage: FC = () => {
  const [chatboxUrl] = useChatBoxUrlState()
  const [loggedIn, setLoggedIn] = useState(false)
  useEffect(() => {
    const effect = async () => {
      if (loggedIn === false) {
        try {
          if (isValidUrl()) {
            await fetchStreamlabsConfig()
            setLoggedIn(true)
          }
        } catch (error) {
          console.error(error)
        }
      }
    }
    effect()
  }, [loggedIn, chatboxUrl])
  return (
    <Layout style={{ minHeight: '100vh' }}>
      <Sidebar showPageSelector={loggedIn} />
      <Layout className="site-layout">
        {loggedIn === false && typeof chatboxUrl === 'string' && <Spin />}
        {loggedIn ? (
          <Switch>
            <Route path="/:pageId/live_videos">
              <LiveVideosDashboard />
            </Route>
            <Route path="/:pageId/advanced_giveaway">
              <AdvancedGiveaway />
            </Route>
            <Route path="/">
              <PageSelector />
            </Route>
          </Switch>
        ) : (
          <PageSelector />
        )}

        <Footer style={{ textAlign: 'center' }}>Ale Ornelas ©2020</Footer>
      </Layout>
    </Layout>
  )
}

const App: FC = () => {
  return (
    <BrowserRouter basename="alegaming_site">
      <Switch>
        <Route path="/widgets*">
          <Widgets />
        </Route>
        <Route path="*">
          <NormalPage />
        </Route>
      </Switch>
    </BrowserRouter>
  )
}

export default App
