import React, { FC } from 'react'
import { Redirect, Route, Switch, useRouteMatch } from 'react-router-dom'
import { RoundsWidget } from './RoundsWidget'

export const Widgets: FC = () => {
  let match = useRouteMatch()

  return (
    <Switch>
      <Switch>
        <Route path={`${match.path}/rounds`}><RoundsWidget /></Route>
        <Route path={match.path}>This could be a loverly widgets list</Route>
        <Route path="*"><Redirect to="/" /></Route>
      </Switch>
    </Switch>
  )
}
